import {getComponentByRootPath} from './util'

const rootPath = '/traceCodeManage';
let getComponent;

//,
getComponent = getComponentByRootPath(rootPath);

const getPath = function (path: string) {
    return `${rootPath}${path}`;
}

export default [
    {
        path: rootPath,
        name: ' ',
        icon: 'icon-uniE05B',
        meta: {
            isMenu: true,
        },
        component: getComponent('Index'),
        children: [
            {
                path: getPath('/traceCodeInfo'),
                name: '',
                meta:{
                    isChildren:false
                },
                component: getComponent('traceCodeInfo/Index'),
            }
        ],
    },

];